/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-caes-hub-toc {
    a {
        color: var(--wp--preset--color--contrast);
    }
    h2, h3, h4, h5, h6 {
        font-size: var(--wp--preset--font-size--large);
    }
    ul {
        padding-inline-start: var(--wp--preset--spacing--40);
    }
    ul.list-none {
        padding-inline-start: 0;
        list-style: none;

        ul.list-none {
            padding-inline-start: revert;
        }
    }
    & > *:first-child {
        margin-top: 0;
    }
    & > *:last-child {
        margin-bottom: 0;
    }
}

.sticky-toc {
    position: fixed;
    transform: translateX(-200%);
    top: var(--wp--style--block-gap);
    left: 0;
    z-index: 1000;
    transition: transform 0.3s ease-in-out, left 0.3s ease-in-out;
    width: 222px;
    max-height: 90vh;
    overflow-y: auto;
    background: var(--wp--preset--color--base);
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    box-shadow: var(--wp--preset--shadow--small);
    padding: var(--wp--preset--spacing--50);
    line-height: 1.3;
    font-size: var(--wp--preset--font-size--small);
    border: 1px solid var(--wp--preset--color--base-three);
    border-left: 0;
    h2, h3, h4, h5, h6 {
        font-family: var(--wp--preset--font-family--merriweather);
        font-size: var(--wp--preset--font-size--normal);
    }
    a {
        text-decoration: none;
    }
    ul {
        list-style: none;
        padding-inline-start: 18px !important;
        li {
            margin: var(--wp--preset--spacing--20) 0;
            &.active {
                font-weight: bold;
                &::marker {
                    content: '\279E\00A0\00A0';
                    color: var(--wp--preset--color--bulldog-red);
                }
            }
        }
        padding: 0;
        ul {
            padding-left: var(--wp--preset--spacing--40);
        }
    }
  }
  
  @media (max-width: 1440px) {
    .sticky-toc {
      display: none; /* Hide on mobile */
    }
  }

  @media print {
    .sticky-toc {
      display: none !important; /* Hide on print */
    }
  }